// InteractionManager.js
var InteractionManager = pc.createScript('interactionManager');

// entity with camera component
InteractionManager.attributes.add('cameraEntity', {
    type: 'entity'
});

InteractionManager.prototype.initialize = function() {
    var app = this.app;
    
    this.selectedEntity = null;
    this.worldPos = new pc.Vec3();
    this.isAction = false;
    this.isDragging = false;
    this.timer = 0;

    if(app.touch) {
        app.touch.on(pc.EVENT_TOUCHSTART, this.onTouchStart, this);
        app.touch.on(pc.EVENT_TOUCHEND, this.onTouchEnd, this);
        app.touch.on(pc.EVENT_TOUCHMOVE, this.onTouchMove, this);
    }
};

InteractionManager.prototype.onTouchStart = function (e) {
    this.isAction = true;
};

InteractionManager.prototype.onTouchEnd = function (e) {
    var app = this.app;
    var self = this;
    
    this.isAction = false;
    
    if(this.isDragging) {
        this.isDragging = false;
        return;  
    }
    
    if (e.changedTouches.length !== 1) {
        return;
    }
        
    var touch = e.changedTouches[0];
    var result = this.onSelect(touch.x, touch.y);
    
    e.event.preventDefault();
};

InteractionManager.prototype.onTouchMove = function (e) {
    if(this.isAction) {
        this.isDragging = true;
    }
};

InteractionManager.prototype.onSelect = function (x, y) {
    var app = this.app;
    
    var from = this.cameraEntity.camera.screenToWorld(x, y, this.cameraEntity.camera.nearClip);
    var to = this.cameraEntity.camera.screenToWorld(x, y, this.cameraEntity.camera.farClip);

    var result = app.systems.rigidbody.raycastFirst(from, to); 
        
    if(result) {
        
        var selectedEntity = result.entity;

        if(selectedEntity.tags.has('clickable')) {
            //console.log('clicked!');
            app.fire('game:activate', selectedEntity);
        }
    }
    
    return !(result === null || result === undefined);
};

