// camera-aspect.js
var CameraAspect = pc.createScript('cameraAspect');

CameraAspect.attributes.add('PortraitModeTrigger', { type: 'number', default: 1.65 });

// initialize code called once per entity
CameraAspect.prototype.initialize = function() {
};

CameraAspect.prototype.postInitialize = function() {    
    var app = this.app;
    
    this.isLandscape = true;

    var canvas = this.app.graphicsDevice.canvas;
    this.currentAspectRatio = canvas.width / canvas.height;

    if(this.currentAspectRatio <= this.PortraitModeTrigger) {
        this.isLandscape = false;
        app.fire('game:portrait');
    }
};

CameraAspect.prototype.update = function(t) {
    var app = this.app;
    
    var canvas = this.app.graphicsDevice.canvas;
    var aspectRatio = canvas.width / canvas.height;

    if (aspectRatio !== this.currentAspectRatio) {

        if(aspectRatio <= this.PortraitModeTrigger && this.isLandscape) {
            this.isLandscape = false;
            app.fire('game:portrait');
        }
        else {
            if(aspectRatio > this.PortraitModeTrigger && !this.isLandscape) {
                this.isLandscape = true;
                app.fire('game:landscape');
            }
        }

        this.currentAspectRatio = aspectRatio;
    }   
};


