// infoBoardMgr.js
var InfoBoardMgr = pc.createScript('infoBoardMgr');

InfoBoardMgr.attributes.add('VideoBoard', { type: 'entity' });
InfoBoardMgr.attributes.add('VideoPlayer', { type: 'entity' });

InfoBoardMgr.attributes.add('popupWidth', { type:'number', default: 640 });
InfoBoardMgr.attributes.add('popupHeight', { type:'number', default: 300 });

InfoBoardMgr.attributes.add('initPos', { type: 'vec3' });
InfoBoardMgr.attributes.add('togoPortrait', { type: 'vec3' });
InfoBoardMgr.attributes.add('togoLandscape', { type: 'vec3' });

InfoBoardMgr.attributes.add('durationIn', {
    type: 'number',
    default: 0.1,
    description: 'Tween animation duration'
});

InfoBoardMgr.attributes.add('durationOut', {
    type: 'number',
    default: 0.1,
    description: 'Tween animation duration'
});

// initialize code called once per entity
InfoBoardMgr.prototype.initialize = function() {
    var app = this.app;
    
    this.buttonUrl = '';
    this.tween = null;
    this.isVisible = false;
    this.viewMode = 'Portrait';
    
    app.on('infoboard:playVideo', function (url) {

        if(url === this.buttonUrl) {
            app.fire('game:closeboard');
            return;
        }  
        
        this.buttonUrl = url;
        
        if(!this.isVisible)
            this.show(); 
        else
            this.VideoPlayer.script.mediaPlayer.initVideo(this.buttonUrl);
        
    }, this);
    
    app.on('infoboard:close', function () {

        this.buttonUrl = '';
        
        if(this.isVisible)
            this.hide(); 
        else
            this.VideoPlayer.script.mediaPlayer.stopVideo(); 
      
    }, this);
    
    app.on('game:landscape', function () {
        if(this.viewMode === 'Landscape')
            return;
        
        this.viewMode = 'Landscape';
        
        //we can see it, need to move it
        if(this.isVisible) {
            if(this.tween)
                this.tween.stop();
            
            this.isRunning = false;
            this.show();
        }
        
    }, this);
    
    app.on('game:portrait', function () {
        if(this.viewMode === 'Portrait')
            return;
        
        this.viewMode = 'Portrait';
        
        //we can see it, need to move it
        if(this.isVisible) {
            if(this.tween)
                this.tween.stop();
            
            this.isRunning = false;
            this.show();
        }
        
    }, this);
};

InfoBoardMgr.prototype.show = function () {

    var self = this;
    if(!this.isVisible)
        this.VideoPlayer.script.mediaPlayer.initVideo(this.buttonUrl);
    
    if(!this.isRunning) {
        this.isRunning = true;
        this.isVisible = true;
        
        var togo = this.viewMode !== 'Portrait' ? this.togoLandscape : this.togoPortrait;
        
        this.tween = this.VideoBoard.tween(this.VideoBoard.getLocalPosition()).to({x: togo.x, y: togo.y, z: togo.z}, this.durationIn, pc.SineInOut); 
        
        this.tween.on('complete', function () {
            self.isRunning = false;
        });  

        this.tween.start();
    }
    
};

InfoBoardMgr.prototype.hide = function () {

    var self = this;

    if(!this.isRunning) {
        this.isRunning = true;
        this.isVisible = false;
    
        this.tween = this.VideoBoard.tween(this.VideoBoard.getLocalPosition()).to({x: this.initPos.x, y: this.initPos.y, z: this.initPos.z}, this.durationOut, pc.SineInOut);
        
        this.tween.on('complete', function () {
            self.isRunning = false;
            self.VideoPlayer.script.mediaPlayer.stopVideo();
        });  

        this.tween.start();
    }
    
};


