// GameManager.js
var GameManagerBike = pc.createScript('gameManager');

GameManagerBike.attributes.add('TutorialUI', { type: 'entity', title: "TutorialUI", description: "Tutorial UI to Hide" });
GameManagerBike.attributes.add('bike', { type: 'entity', title: "Bike"});
GameManagerBike.attributes.add('floor', { type: 'entity', title: "Floor" });
GameManagerBike.attributes.add('TimeDeactPhysic', { type: 'number', title: "TimeDeactPhysic", default: 1000 });

GameManagerBike.attributes.add('tutoIntro', { type: 'entity' });
GameManagerBike.attributes.add('camera', { type: 'entity' });
GameManagerBike.attributes.add('motorBorder', { type: 'entity' });
GameManagerBike.attributes.add('batteryBorder', { type: 'entity' });
GameManagerBike.attributes.add('driveBorder', { type: 'entity' });
GameManagerBike.attributes.add('lineMotor', { type: 'entity' });
GameManagerBike.attributes.add('lineBattery', { type: 'entity' });
GameManagerBike.attributes.add('lineDrive', { type: 'entity' });
GameManagerBike.attributes.add('circleAnim', { type: 'entity' });

GameManagerBike.attributes.add('TimeDisplayInfo', { type: 'number', title: "TimeDisplayInfo", default: 2000 });

GameManagerBike.attributes.add('landscapeMobileScale', { type: 'number', default: 0.02, description: 'Landscape mobile scale'});
GameManagerBike.attributes.add('landscapeDesktopScale', { type: 'number', default: 0.02, description: 'Landscape desktop scale'});
GameManagerBike.attributes.add('portraitMobileScale', { type: 'number', default: 0.028, description: 'Portrait mobile scale'});
GameManagerBike.attributes.add('portraitDesktopScale', { type: 'number', default: 0.022, description: 'Portrait desktop scale'});

GameManagerBike.attributes.add('rentPage', { type: 'entity' });
GameManagerBike.attributes.add('sharePage', { type: 'entity' });
GameManagerBike.attributes.add('buyPage', { type: 'entity' });
GameManagerBike.attributes.add('addPage', { type: 'entity' });
GameManagerBike.attributes.add('payPage', { type: 'entity' });
GameManagerBike.attributes.add('engineInfoPage', { type: 'entity' });
GameManagerBike.attributes.add('batteryInfoPage', { type: 'entity' });
GameManagerBike.attributes.add('bikeTypeInfoPage', { type: 'entity' });


// initialize code called once per entity
GameManagerBike.prototype.initialize = function() {
    var app = this.app;
    var self = this;
    this._window = null;
    this.rayColor = new pc.Color(1, 0, 0);
    this.tmp = new pc.Vec3();
    this.myTimeout = null;
    this.bumpCount = 0;
    this.currentUrl = '';
    
    this.choiceMotor = -1;
    this.choiceBattery = -1;
    this.choiceType = -1;
    
    this.bikeX = this.bike.getLocalEulerAngles().x;
    this.bikeY = this.bike.getLocalEulerAngles().y;
    this.bikeZ = this.bike.getLocalEulerAngles().z;
    
    app.addTweenManager();
    
    this.viewMode = 'Portrait';
    this.UpdateVisuals(this.viewMode === 'Portrait');
    
 
    this.bike.collision.on('collisionstart', this.bikeOnFloor, this);
    this.bike.collision.on('contact', this.bikeBump, this);
    this.needUpdate = false;
    
    app.on('game:landscape', function () {
        var self = this;
        
        if(this.viewMode === 'Landscape')
            return;
        
        this.viewMode = 'Landscape';
        //app.fire('flash:blackIn');
        
        setTimeout(function () {
            self.UpdateVisuals(self.viewMode === 'Landscape');
            console.log(self.bike.getPosition());
            console.log(self.camera.getPosition());
            //app.fire('flash:blackOut');
        }, 100);
        
    }, this);
    
    app.on('game:portrait', function () {
        var self = this;
        
        if(self.viewMode === 'Portrait')
            return; 
        
        self.viewMode = 'Portrait';
        //app.fire('flash:blackIn');
        
        setTimeout(function () {
            self.UpdateVisuals(self.viewMode === 'Landscape');
            console.log(self.bike.getPosition());
            console.log(self.camera.getPosition());
            //app.fire('flash:blackOut');
        }, 100);
    
    }, this);
    
    app.on('game:activate', function (entity) {
        var self = this;
        var app = this.app;
        
        entity.script.pulse.pulse();
        app.fire('game:playSound', 'BtnClick');
        
        setTimeout(function () {   
            setTimeout(function () {
                if(entity.tags.has('url')) {
                    entity.script.launchUrl.launch();
                }
            }, 600);
        }, 100);
        
    }, this);
    
    this.isAction1 = false;
    app.on('game:action1', function (url) { 
        var app = this.app;
        
        app.fire('game:infosOff');
        this.isAction2 = false;
        this.isAstion3 = false;
        this.isAction1 = !this.isAction1;
        this.currentUrl = '';
        
        if(this.isAction1) {
            this.currentUrl = url;
            app.fire('uiMgr:hideMain');
            app.fire('uiMgr:showOption1');
            app.fire('uiMgr:hideOption2');
            app.fire('uiMgr:firstOptionText', '1000 W', this.choiceMotor === 0);
            app.fire('uiMgr:secondOptionText', '2000 W', this.choiceMotor === 1);
            app.fire('uiMgr:firstOptionCheckMark',  this.choiceMotor === 0);
            app.fire('uiMgr:secondOptionCheckMark',  this.choiceMotor === 1);
            app.fire('game:motorInfoOn');
            
            if(this.choiceMotor === 0) {
                app.fire('uiMgr:videoText', '2021 Valtechtronic 1000W Motor');
            }
            else if(this.choiceMotor === 1) {
                app.fire('uiMgr:videoText', '2021 Valtechtronic 2000W Motor');
            }
            else {
                app.fire('uiMgr:videoText', 'Motor Power');
            }
        }
        
        app.fire('infoboard:playVideo', url);
        this.needUpdate = true;
    }, this);
    
    this.isAction2 = false;
    app.on('game:action2', function (url) {
        var app = this.app;
        
        app.fire('game:infosOff');
        this.isAction1 = false;
        this.isAction3 = false;
        this.isAction2 = !this.isAction2;
        this.currentUrl = '';
        
        if(this.isAction2) {
            this.currentUrl = url;
            app.fire('uiMgr:hideMain');
            app.fire('uiMgr:showOption1');
            app.fire('uiMgr:hideOption2');
            app.fire('uiMgr:firstOptionText', '500 W/h', this.choiceBattery === 0);
            app.fire('uiMgr:secondOptionText', '1000 W/h', this.choiceBattery === 1);
            app.fire('uiMgr:firstOptionCheckMark',  this.choiceBattery === 0);
            app.fire('uiMgr:secondOptionCheckMark',  this.choiceBattery === 1);
            app.fire('game:batteryInfoOn');
            
            if(this.choiceBattery === 0) {
                app.fire('uiMgr:videoText', '2021 Valtechtronic 500W/h Motor');
            }
            else if(this.choiceBattery === 1) {
                app.fire('uiMgr:videoText', '2021 Valtechtronic 1000W/h Motor');
            }
            else {
                app.fire('uiMgr:videoText', 'Battery');
            }
        }
        
        app.fire('infoboard:playVideo', url);
        this.needUpdate = true;
    }, this);
    
    this.isAction3 = false;
    app.on('game:action3', function (url) {
        var app = this.app;
        
        app.fire('game:infosOff');
        this.isAction1 = false;
        this.isAction2 = false;
        this.isAction3 = !this.isAction3;
        this.currentUrl = '';
        
        if(this.isAction3) {
            this.currentUrl = url;
            app.fire('uiMgr:hideMain');
            app.fire('uiMgr:showOption1');
            app.fire('uiMgr:hideOption2');
            app.fire('uiMgr:firstOptionText', 'Wheel-Drive', this.choiceType === 0);
            app.fire('uiMgr:secondOptionText', 'Hub-Drive', this.choiceType === 1);
            app.fire('uiMgr:firstOptionCheckMark',  this.choiceType === 0);
            app.fire('uiMgr:secondOptionCheckMark',  this.choiceType === 1);
            app.fire('game:driveInfoOn');
            
            if(this.choiceType === 0) {
                app.fire('uiMgr:videoText', '2021 Valtechtronic Wheel-Drive');
            }
            else if(this.choiceType === 1) {
                app.fire('uiMgr:videoText', '2021 Valtechtronic Hub-Drive');
            }
            else {
                app.fire('uiMgr:videoText', 'Drive type');
            }
        }
        
        app.fire('infoboard:playVideo', url);
        this.needUpdate = true;
    }, this);
    
    app.on('game:choiceDone1', function () { 
        this.app.fire('uiMgr:firstOptionCheckMark', true);
        this.app.fire('uiMgr:secondOptionCheckMark', false);
        
        if(this.isAction1) {
            this.choiceMotor = 0;
            this.app.fire('uiMgr:videoText', '2021 Valtechtronic 2000W Motor');
            this.app.fire('uiMgr:checkmarkMotor');
        }
        else if(this.isAction2) {
            this.choiceBattery = 0;
            this.app.fire('uiMgr:videoText', '2021 Valtechtronic 500W/h Motor');
            this.app.fire('uiMgr:checkmarkBattery');
        }
        else if(this.isAction3) {
            this.choiceType = 0;
            this.app.fire('uiMgr:videoText', '2021 Valtechtronic Pedal-Assist');
            this.app.fire('uiMgr:checkmarkType');
        }
        
    }, this);
    
    app.on('game:choiceDone2', function () { 
        this.app.fire('uiMgr:firstOptionCheckMark', false);
        this.app.fire('uiMgr:secondOptionCheckMark', true);
        
        if(this.isAction1) {
            this.choiceMotor = 1;
            this.app.fire('uiMgr:videoText', '2021 Valtechtronic 4000W Motor');
            this.app.fire('uiMgr:checkmarkMotor');
        }
        else if(this.isAction2) {
            this.choiceBattery = 1;
            this.app.fire('uiMgr:videoText', '2021 Valtechtronic 1000W/h Motor');
            this.app.fire('uiMgr:checkmarkBattery');
        }
        else if(this.isAction3) {
            this.choiceType = 1;
            this.app.fire('uiMgr:videoText', '2021 Valtechtronic Throttle');
            this.app.fire('uiMgr:checkmarkType');
        }
    }, this);
    
    app.on('game:moreInfo1', function () { 
      
        var app = this.app;
         
        if(this.isAction1) {
            if(!this.engineInfoPage)
                return;
        
            app.fire('uiMgr:hideOption1');
            app.fire('infoboard:close');
            app.fire('game:infosOff');
            this.engineInfoPage.script.uiMotor.showPage();
        }
        else if(this.isAction2) {
            if(!this.batteryInfoPage)
                return;
        
            app.fire('uiMgr:hideOption1');
            app.fire('infoboard:close');
            app.fire('game:infosOff');
            this.batteryInfoPage.script.uiBattery.showPage();
        }
        else if(this.isAction3) {
            if(!this.bikeTypeInfoPage)
                return;
        
            app.fire('uiMgr:hideOption1');
            app.fire('infoboard:close');
            app.fire('game:infosOff');
            this.bikeTypeInfoPage.script.uiTypes.showPage();
        }
        
    }, this);
    
    app.on('game:moreInfo2', function () { 
       
        var app = this.app;
        
        if(this.isAction1) {
            if(!this.engineInfoPage)
                return;
        
            app.fire('uiMgr:hideOption1');
            app.fire('infoboard:close');
            app.fire('game:infosOff');
            this.engineInfoPage.script.uiMotor.showPage2();
        }
        else if(this.isAction2) {
            if(!this.batteryInfoPage)
                return;
        
            app.fire('uiMgr:hideOption1');
            app.fire('infoboard:close');
            app.fire('game:infosOff');
            this.batteryInfoPage.script.uiBattery.showPage2();
        }
        else if(this.isAction3) {
            if(!this.bikeTypeInfoPage)
                return;
    
            app.fire('uiMgr:hideOption1');
            app.fire('infoboard:close');
            app.fire('game:infosOff');
            this.bikeTypeInfoPage.script.uiTypes.showPage2();
        }
    }, this);
    
    app.on('game:selectcolor', function () { 
        this.app.fire('uiMgr:hideOption1');
        this.app.fire('uiMgr:showColors');
    }, this);
    
    app.on('game:closecolor', function () { 
        this.app.fire('uiMgr:hideOption2');
    }, this);
    
    app.on('game:colorgreen', function () { 
        this.app.fire('button:displaygreen');
        this.app.fire('uiMgr:checkMarkColor');
        
        this.app.fire('uiMgr:checkMarkWhite', false);
        this.app.fire('uiMgr:checkMarkBlue', false);
        this.app.fire('uiMgr:checkMarkGreen', true);
        this.app.fire('uiMgr:checkMarkOrange', false);
        
    }, this);
    
    app.on('game:colororange', function () { 
        this.app.fire('button:displayorange');
        this.app.fire('uiMgr:checkMarkColor');
        
        this.app.fire('uiMgr:checkMarkWhite', false);
        this.app.fire('uiMgr:checkMarkBlue', false);
        this.app.fire('uiMgr:checkMarkGreen', false);
        this.app.fire('uiMgr:checkMarkOrange', true);
        
    }, this);
    
    app.on('game:colorwhite', function () { 
        this.app.fire('button:displaywhite');
        this.app.fire('uiMgr:checkMarkColor');
        
        this.app.fire('uiMgr:checkMarkWhite', true);
        this.app.fire('uiMgr:checkMarkBlue', false);
        this.app.fire('uiMgr:checkMarkGreen', false);
        this.app.fire('uiMgr:checkMarkOrange', false);
        
    }, this);
    
    app.on('game:colorblue', function () { 
        this.app.fire('button:displayblue');
        this.app.fire('uiMgr:checkMarkColor');
        
        this.app.fire('uiMgr:checkMarkWhite', false);
        this.app.fire('uiMgr:checkMarkBlue', true);
        this.app.fire('uiMgr:checkMarkGreen', false);
        this.app.fire('uiMgr:checkMarkOrange', false);
        
    }, this);
    
    app.on('game:closeboard', function () {
        this.isAction1 = false;
        this.isAction2 = false;
        this.isAction3 = false;
        this.needUpdate = true;
        
        this.app.fire('uiMgr:showMain');
        this.app.fire('uiMgr:hideOption1');
        this.app.fire('infoboard:close');
        this.app.fire('game:infosOff');
    }, this);
    
    app.on('game:hidetutorial', function () {
        this.TutorialUI.enabled = false;
    }, this);
    
    app.on('bike:playSound', function (slot) {
        this.bike.sound.play(slot);
    }, this);
    
    app.on('bike:stopSound', function (slot) {
        this.bike.sound.stop(slot);
    }, this);
    
    app.on('game:playSound', function (slot) {
        this.entity.sound.play(slot);
    }, this);
    
    app.on('game:stopSound', function (slot) {
        this.entity.sound.stop(slot);
    }, this);
    
    app.on('game:activatephysics', function() {
        var self = this;
        
        this.circleAnim.enabled = false;
        this.bike.setEulerAngles(this.bikeX, this.bikeY + this.camera.getEulerAngles().y, this.bikeZ);
        this.bike.setPosition(this.circleAnim.getPosition().x, this.bike.getPosition().y, this.circleAnim.getPosition().z);
        this.bike.enabled = true;
        this.bike.rigidbody.enabled = true;
        
        setTimeout(function () {
            self.bike.rigidbody.enabled = false;
        }, this.TimeDeactPhysic);
    }, this);
   
    app.on('game:infosOff', function () {
        this.motorBorder.enabled = false;
        this.batteryBorder.enabled = false;
        this.driveBorder.enabled = false;
        
        this.lineMotor.script.lineBtwnEntity.rayColor.fromString('#36C8B0');
        this.lineBattery.script.lineBtwnEntity.rayColor.fromString('#36C8B0');
        this.lineDrive.script.lineBtwnEntity.rayColor.fromString('#36C8B0');
    }, this);
    
    app.on('game:motorInfoOn', function () {
        this.lineMotor.script.lineBtwnEntity.rayColor.fromString('#FF0000');
        this.motorBorder.enabled = true;
    }, this);
    
    app.on('game:batteryInfoOn', function () { 
        this.lineBattery.script.lineBtwnEntity.rayColor.fromString('#FF0000');
        this.batteryBorder.enabled = true;
    }, this);
    
    app.on('game:driveInfoOn', function () { 
        this.lineDrive.script.lineBtwnEntity.rayColor.fromString('#FF0000');
        this.driveBorder.enabled = true;
    }, this);
    
    app.on('game:openSharePage', function () {
        if(!this.sharePage)
            return;

        var app = this.app;
        app.fire('uiMgr:hideMain');
        app.fire('uiMgr:hideOption1');
        app.fire('uiMgr:hideOption2');
        
        this.sharePage.script.uiShare.showPage();
    }, this);
    
    app.on('game:openBuyPage', function () {
        if(!this.buyPage)
            return;
        
        var app = this.app;
        app.fire('uiMgr:hideMain');
        app.fire('uiMgr:hideOption1');
        app.fire('uiMgr:hideOption2');
        
        this.buyPage.script.uiBuy.showPage();
        
    }, this);
    
    app.on('game:openAddPage', function () {
        if(!this.addPage)
            return;
        
        var app = this.app;
        app.fire('uiMgr:hideMain');
        app.fire('uiMgr:hideOption1');
        app.fire('uiMgr:hideOption2');
        
        this.addPage.script.uiAdd.showPage();
        
    }, this);
    
    app.on('game:openPayPage', function () {
        if(!this.payPage)
            return;
        
        var app = this.app;
        app.fire('uiMgr:hideMain');
        app.fire('uiMgr:hideOption1');
        app.fire('uiMgr:hideOption2');
        
        this.payPage.script.uiPay.showPage();
        
    }, this);
    
    app.on('game:openRentPage', function () {
        if(!this.rentPage)
            return;
        
        var app = this.app;
        app.fire('uiMgr:hideMain');
        app.fire('uiMgr:hideOption1');
        app.fire('uiMgr:hideOption2');
        
        this.rentPage.script.uiRide.showPage();
        
    }, this);
    
    app.on('game:closeRentPage', function () {
        this.app.fire('uiMgr:showMain');
    }, this);
    
    app.on('game:closeBuyPage', function () {
        this.app.fire('uiMgr:showMain');
    }, this);
    
    app.on('game:closeAddPage', function () {
        this.app.fire('uiMgr:showMain');
    }, this);
    
    app.on('game:closePayPage', function () {
        this.app.fire('uiMgr:showMain');
    }, this);
    
    app.on('game:closeSharePage', function () {
        this.app.fire('uiMgr:showMain');
    }, this);
    
    app.on('game:closeEngineInfoPage', function () {
        this.isAction1 = false;
        this.isAction2 = false;
        this.isAction3 = false;
        this.app.fire('game:action1', this.currentUrl);
    }, this);
    
    app.on('game:closeBatteryInfoPage', function () {
        this.isAction1 = false;
        this.isAction2 = false;
        this.isAction3 = false;
        this.app.fire('game:action2', this.currentUrl);
    }, this);
    
    app.on('game:closeBikeTypeInfoPage', function () {
        this.isAction1 = false;
        this.isAction2 = false;
        this.isAction3 = false;
        this.app.fire('game:action3', this.currentUrl);
    }, this);
 
    //8thwall stuff
    //
    //
    //
    app.on('game:recenter', function (slot) {
        //app.fire('xr:recenter');
        //
        var app = this.app;
    
        var screenCenterX = (app.graphicsDevice.width / 2) / window.devicePixelRatio;
        var screenCenterY = (app.graphicsDevice.height / 2) / window.devicePixelRatio;

        var from = this.camera.camera.screenToWorld(screenCenterX, screenCenterY, this.camera.camera.nearClip);
        var to = this.camera.camera.screenToWorld(screenCenterX, screenCenterY, this.camera.camera.farClip);
        this.doRaycast(from, to);
    }, this);

    app.on('xr:realityready', function() {
        console.log('xr ready!');
    
        var self = this;
        var app = this.app;

        self.tutoIntro.enabled = true;
        self.bike.enabled = false;
        app.fire('taprecenter:ready');
        app.fire('pulse:start');
        
    }, this);
};

GameManagerBike.prototype.postInitialize = function() {  
    var app = this.app;
    
};

GameManagerBike.prototype.UpdateVisuals = function(isLandscape) {
    var app = this.app;
    
    if(isLandscape) { 
        /*app.root.findByName('AR Marker').findByName('Portrait').findByName('Visuals').enabled = false;
        app.root.findByName('AR Marker').findByName('Landscape').findByName('Visuals').enabled = true;*/
    } 
    else { 
        /*app.root.findByName('AR Marker').findByName('Portrait').findByName('Visuals').enabled = true;
        app.root.findByName('AR Marker').findByName('Landscape').findByName('Visuals').enabled = false;*/
    } 
};

// update code called every frame
GameManagerBike.prototype.bikeOnFloor = function() {
    var app = this.app;
    var self = this;
    
    this.bike.collision.off('collisionstart', this.bikeOnFloor, this);
    
    app.fire('game:playSound', 'music');
    app.fire('uiMgr:showMain');
};

GameManagerBike.prototype.bikeBump = function() {
    var app = this.app;
    var self = this;
    
    ++this.bumpCount;
    
    app.fire('game:playSound', 'bump');
    
    if(this.bumpCount === 3)
        this.bike.collision.off('contact', this.bikeBump, this);
};

// update code called every frame
GameManagerBike.prototype.update = function(dt) {
    TWEEN.update();
    
    if(!this.needUpdate)
        return;
    
    this.needUpdate = false;
    if(!this.isAction1 && !this.isAction2 && !this.isAction3) {
        this.app.fire('music:fadeIn', 'music');
    }
    else {
        this.app.fire('music:fadeOut', 'music');
    }
};

GameManagerBike.prototype.doRaycast = function (from, to) {
    var app = this.app;
    var rayResult = app.systems.rigidbody.raycastFirst(from, to); 
      
    if(rayResult) {
        this.bike.setEulerAngles(this.bikeX, this.bikeY + this.camera.getEulerAngles().y, this.bikeZ);
        this.bike.setPosition(rayResult.point.x, this.bike.getPosition().y, rayResult.point.z);
    }
};   


