// fade.js
var Fade = pc.createScript('fade');

Fade.attributes.add('event', { type:'string' });
Fade.attributes.add('type', {
    type: 'number',
    enum: [
        { 'In': 0 },
        { 'Out': 1 },
        { 'InOut': 2 }
    ]
});
Fade.attributes.add('duration', { type:'number', default: 1000 });
Fade.attributes.add('delay', { type:'number', default: 0 });

// initialize code called once per entity
Fade.prototype.initialize = function() {
    var app = this.app;

    app.on(this.event, function () {
        var type = this.type;
        var sprite = this.entity.sprite;
        sprite.opacity = type === 1 ? 1 : 0;

        var time = this.duration;
        if (type === 2) {
            time *= 0.5;
        }
        var tween = new TWEEN.Tween(sprite)
            .to({ opacity: type === 1 ? 0 : 1 }, time)
            .onStart(function () {
                sprite.enabled = true;
            })
            .onComplete(function () {
                sprite.enabled = type === 0;
            })
            .repeat(type === 2 ? 1 : 0)
            .yoyo(type === 2)
            .delay(this.delay)
            .start();
    }, this);
    
    this.on('enable', function () {
        var type = this.type;
        var sprite = this.entity.sprite;
        sprite.enabled = type === 1;
        sprite.opacity = type === 1 ? 1 : 0;
    });
};


