// pulse.js
var Pulse = pc.createScript('pulse');

Pulse.attributes.add('startingEvent', {
    title: 'Event',
    type: 'string'
});

Pulse.attributes.add('loop', {
    title: 'Loop',
    type: 'boolean',
    default: false
});

Pulse.attributes.add('duration', {
    type: 'number',
    default: 0.1,
    description: 'Tween animation duration'
});

Pulse.attributes.add('scaleincrement', {
    type: 'number',
    default: 1,
    description: 'Amout of scaling on pulse animation'
});

// initialize code called once per entity
Pulse.prototype.initialize = function() {
    this.isRunning = false;
    
    this.app.on(this.startingEvent, function () {
        //console.log('pulse start event received');
        this.pulse();
    }, this);
};

Pulse.prototype.pulse = function () {

    var self = this;
    
    if(!this.isRunning)
    {
        //console.log('pulse start running');
        
        this.isRunning = true;
        var scale = this.entity.getLocalScale().clone();
        var tween = this.entity.tween(this.entity.getLocalScale()).to({x: scale.x+this.scaleincrement, y: scale.y+this.scaleincrement, z: scale.z+this.scaleincrement}, this.duration, pc.SineInOut);
        
        if(!this.loop) {
            tween.yoyo(true).repeat(2).on('complete', function () {
                self.isRunning = false;
            }); 
        }
        else {
            tween.loop(true).yoyo(true);
        }    
       
        tween.start();
        //console.log('pulse start started');
    }
    
};


