// button.js
var Button = pc.createScript('button');

Button.attributes.add('sound', { type: 'string' });
Button.attributes.add('event', { type: 'string' });

Button.attributes.add('copyEnabled', { type: 'boolean', default: false });
Button.attributes.add("entityToCopyText", { type: "entity", title: "Entity to copy text"});
Button.attributes.add('eventCopy', { type: 'string', default: 'game:copycoupon'});

Button.attributes.add('switchEnabled', { type: 'boolean', default: false });
Button.attributes.add('stringidstart', { type: 'string' });
Button.attributes.add('stringidfinish', { type: 'string' });
Button.attributes.add('switchMessageTime', { type: 'number', default: 2000 });

// initialize code called once per entity
Button.prototype.initialize = function() {
    var app = this.app;

    this.pressed = false;
    
    this.entity.element.on('click', this.onClick, this);
};

// When we release the element assign the original texture if
// we are not hovering or the hover texture if we are still hovering
Button.prototype.onClick = function (event) {

    var app = this.app;
    //console.log('Playcanvas OnClick');
    
    if(this.entity.button && !this.entity.button.active)
        return;

    if(this.entity.script.pulse)
        this.entity.script.pulse.pulse();
    
    if(this.entity.script.launchUrl)
        this.entity.script.launchUrl.launch();
    
    app.fire(this.event);
    app.fire('game:playSound', this.sound);

    if(this.copyEnabled)
        app.fire(this.eventCopy, this.entityToCopyText.element.text);

    if(this.switchEnabled) {

        var self = this;  

        //text directly on entity?
        if(this.entity.element && this.entity.element.text) {

            this.entity.element.text = app.i18n.getText(this.stringidfinish);

            setTimeout(function () {
                self.entity.element.text = app.i18n.getText(self.stringidstart);
            }, this.switchMessageTime);
        }
        else  { //text element as a child?
            if(this.entity.children.length > 0 && this.entity.children[0]) {
                if(this.entity.children[0].element && this.entity.children[0].element.text) {

                    this.entity.children[0].element.text = app.i18n.getText(this.stringidfinish);

                    setTimeout(function () {
                        self.entity.children[0].element.text = app.i18n.getText(self.stringidstart);
                    }, this.switchMessageTime);
                }
            }  
        }
    }  

};

