// DrawToMiddleScreen.js
var DrawTomiddleScreen = pc.createScript('drawTomiddleScreen');

DrawTomiddleScreen.attributes.add('cameraEntity', {
    type: 'entity'
});

// initialize code called once per entity
DrawTomiddleScreen.prototype.initialize = function() {
    
};

DrawTomiddleScreen.prototype.doRaycast = function (from, to) {
    var app = this.app;
    var self = this;
    
    var rayResult = app.systems.rigidbody.raycastFirst(from, to); 
        
    if(rayResult) {
        //var pickedEntity = rayResult.entity;
        this.entity.sprite.enabled = true;
        this.entity.setPosition(rayResult.point);
    }
    else {
        this.entity.sprite.enabled = false;
    }
};   

// update code called every frame
DrawTomiddleScreen.prototype.update = function(dt) {
    var app = this.app;
    
    var screenCenterX = (app.graphicsDevice.width / 2) / window.devicePixelRatio;
    var screenCenterY = (app.graphicsDevice.height / 2) / window.devicePixelRatio;

    var from = this.cameraEntity.camera.screenToWorld(screenCenterX, screenCenterY, this.cameraEntity.camera.nearClip);
    var to = this.cameraEntity.camera.screenToWorld(screenCenterX, screenCenterY, this.cameraEntity.camera.farClip);
    this.doRaycast(from, to);
};

