// billboard.js
var Billboard = pc.createScript('billboard');

Billboard.attributes.add("camera", { type: "entity", title: "Camera"});

Billboard.attributes.add('xaxis', { type: 'boolean', default: false });
Billboard.attributes.add('yaxis', { type: 'boolean', default: false });
Billboard.attributes.add('zaxis', { type: 'boolean', default: false });

// initialize code called once per entity
Billboard.prototype.initialize = function() {
    this.x = this.entity.getLocalEulerAngles().x;
    this.y = this.entity.getLocalEulerAngles().y;
    this.z = this.entity.getLocalEulerAngles().z;
};

// update code called every frame
Billboard.prototype.update = function(dt) {
    this.entity.setEulerAngles(this.xaxis ? this.x + this.camera.getEulerAngles().x : this.x, this.yaxis ? this.y + this.camera.getEulerAngles().y : this.y, this.zaxis ? this.z + this.camera.getEulerAngles().z : this.z);
};

