// launchUrl.js
var LaunchUrl = pc.createScript('launchUrl');

LaunchUrl.attributes.add('url', { type: 'string' });
LaunchUrl.attributes.add('event', { type: 'string' });
LaunchUrl.attributes.add('isVideo', { type:'boolean', default: false });

// initialize code called once per entity
LaunchUrl.prototype.initialize = function() {
};

// update code called every frame
LaunchUrl.prototype.launch = function() {
    
    this.app.fire(this.event, this.url);
    
    /*if (this.isVideo)
        this.app.fire('infoboard:playVideo', this.url);
    else 
        window.open(this.url); */
};




