// ui-share.js

var Ui = pc.createScript('uiShare');

Ui.attributes.add('css', {type: 'asset', assetType:'css', title: 'css-share'});
Ui.attributes.add('html', {type: 'asset', assetType:'html', title: 'html-share'});
Ui.attributes.add('buttonSound', { type: 'string' });

Ui.prototype.initialize = function () {
    
    var app = this.app;

    // create STYLE element
    var style = document.createElement('style');

    // append to head
    document.head.appendChild(style);
    style.innerHTML = this.css.resource || '';
    
    // Add the HTML
    this.div = document.createElement('div');
    this.div.classList.add('container-share');
    this.div.innerHTML = this.html.resource || '';
    
    var path3 = this.app.assets.find('path3@3x.png').getFileUrl();
    
    // Replace tags in the html 
    // header
    this.div.innerHTML = this.div.innerHTML.replace('{closex-image}', app.assets.find('close.png').getFileUrl());

    // product
    this.div.innerHTML = this.div.innerHTML.replace('{ebike-image}', app.assets.find('ebike.png').getFileUrl());
  
    // social media
    this.div.innerHTML = this.div.innerHTML.replace('{facebook-image}', app.assets.find('Facebook.png').getFileUrl());
    this.div.innerHTML = this.div.innerHTML.replace('{redit-image}', app.assets.find('Reddit.png').getFileUrl());
    this.div.innerHTML = this.div.innerHTML.replace('{snapchat-image}', app.assets.find('Snapchat.png').getFileUrl());
    this.div.innerHTML = this.div.innerHTML.replace('{twitter-image}', app.assets.find('Twitter.png').getFileUrl());
    this.div.innerHTML = this.div.innerHTML.replace('{whatsapp-image}', app.assets.find('WhatsApp.png').getFileUrl());
    
    this.pageIsShown = false;
};

Ui.prototype.showPage = function() {
    if(this.pageIsShown)
        return;
    
    this.pageIsShown = true;
    
    var app = this.app;
    var self = this;
    
    document.body.appendChild(this.div);
    
    this.btnXClose = document.getElementById('btn-closeX');
    if(this.btnXClose) {
        this.btnXClose.onclick = function() { 
            app.fire('game:playSound', self.buttonSound);
            self.hidePage();
            app.fire('game:closeSharePage');
        };
    }
};



Ui.prototype.hidePage = function() {
    if(!this.pageIsShown)
        return;
    
    this.pageIsShown = false;

    this.btnXClose = document.getElementById('btn-closeX');
    if(this.btnXClose) {
        this.btnXClose.onclick = null;
    }
    
    document.body.removeChild(this.div);
};


