// LineBtwnEntity.js
var LineBtwnEntity = pc.createScript('lineBtwnEntity');

LineBtwnEntity.attributes.add('from', { type: 'entity' });

LineBtwnEntity.attributes.add('to', { type: 'entity' });

LineBtwnEntity.attributes.add('rayColor', {
    type:"rgb",
    default:[1,0,0]
});

// initialize code called once per entity
LineBtwnEntity.prototype.initialize = function() {
    
};

LineBtwnEntity.prototype.update = function(dt) {
    this.app.renderLine(this.from.getPosition(), this.to.getPosition(), this.rayColor);
};


