// HoverChangeColor.js
var HoverChangeColor = pc.createScript('hoverChangeColor');

HoverChangeColor.attributes.add("hoverColor",{
    type:"rgb",
    default:[0,0,1]
});

// initialize code called once per entity
HoverChangeColor.prototype.initialize = function() {
    this.originalColor = this.entity.model.material.diffuse.clone();
    //console.log('color ' + this.originalColor);
    this.tweenHoverOnDone = true;
    this.tweenHoverOffDone = true;
};

HoverChangeColor.prototype.hoverOn = function(duration) 
{
    var app = this.app;
    var self = this;
    
    if(!this.tweenHoverOnDone)
        return;
    
    //console.log('hoveron : ' + this.entity.name);
    this.tweenHoverOnDone = true;
    
    var material = this.entity.model.material;
    var color = this.originalColor.clone();

    var currentTween = this.entity.tween(color).to(self.hoverColor, duration, pc.Linear);
    
    currentTween.on('update', function () {
        material.diffuse = color;
        material.update();
    });
    
    currentTween.on('complete', function () { 
        material.diffuse = self.hoverColor;
        material.update();
        self.tweenHoverOnDone = true;
    });
    
    currentTween.start();
};


HoverChangeColor.prototype.hoverOff = function(duration) 
{
    var app = this.app;
    var self = this;
    
    if(!this.tweenHoverOffDone)
        return;
    
    //console.log('hoveroff : ' + this.entity.name);
    this.tweenHoverOffDone = true;
    
    var material = this.entity.model.material;
    var color = material.diffuse.clone();

    var currentTween = this.entity.tween(color).to(self.originalColor, duration, pc.Linear);
    
    currentTween.on('update', function () {
        material.diffuse = color;
        material.update();
    });
    
    currentTween.on('complete', function () { 
        material.diffuse = self.originalColor;
        material.update();
        self.tweenHoverOffDone = true;
    });
    
    currentTween.start();
 
};

