// tweener.js
var Tweener = pc.createScript('tweener');

Tweener.attributes.add('event', {
    title: 'Event',
    type: 'string'
});
Tweener.attributes.add('duration', {
    title: 'Duration',
    type: 'number',
    default: 1000
});
Tweener.attributes.add('easingFunction', {
    title: 'Easing Function',
    type: 'number',
    enum: [
        { 'Linear': 0 },
        { 'Quadratic': 1 },
        { 'Cubic': 2 },
        { 'Quartic': 3 },
        { 'Quintic': 4 },
        { 'Sinusoidal': 5 },
        { 'Exponential': 6 },
        { 'Circular': 7 },
        { 'Elastic': 8 },
        { 'Back': 9 },
        { 'Bounce': 10 }
    ]
});
Tweener.attributes.add('easingType', { 
    title: 'Easing Type',
    type: 'number',
    enum: [
        { 'In': 0 },
        { 'Out': 1 },
        { 'InOut': 2 }
    ]
});
Tweener.attributes.add('delay', {
    title: 'Delay',
    type: 'number',
    default: 0
});
Tweener.attributes.add('repeat', {
    title: 'Repeat',
    type: 'number',
    default: 0
});
Tweener.attributes.add('repeatDelay', {
    title: 'Repeat Delay',
    type: 'number',
    default: 0
});
Tweener.attributes.add('yoyo', {
    title: 'Yoyo',
    type: 'boolean',
    default: false
});
Tweener.attributes.add('startPos', {
    title: 'Start Pos',
    type: 'vec3',
    default: [0, 0, 0]
});
Tweener.attributes.add('endPos', {
    title: 'End Pos',
    type: 'vec3',
    default: [0, 0, 0]
});
Tweener.attributes.add('startRot', {
    title: 'Start Rot',
    type: 'vec3',
    default: [0, 0, 0]
});
Tweener.attributes.add('endRot', {
    title: 'End Rot',
    type: 'vec3',
    default: [0, 0, 0]
});
Tweener.attributes.add('startScale', {
    title: 'Start Scale',
    type: 'vec3',
    default: [1, 1, 1]
});
Tweener.attributes.add('endScale', {
    title: 'End Scale',
    type: 'vec3',
    default: [1, 1, 1]
});
Tweener.attributes.add('startEvent', {
    title: 'Start Event',
    type: 'string'
});
Tweener.attributes.add('stopEvent', {
    title: 'Stop Event',
    type: 'string'
});
Tweener.attributes.add('updateEvent', {
    title: 'Update Event',
    type: 'string'
});
Tweener.attributes.add('completeEvent', {
    title: 'Complete Event',
    type: 'string'
});
Tweener.attributes.add('repeatEvent', {
    title: 'Repeat Event',
    type: 'string'
});

// initialize code called once per entity
Tweener.prototype.initialize = function() {
    var app = this.app;

    this.initialPos = this.entity.getPosition().clone();
    this.initialScl = this.entity.getLocalScale().clone();
    this.initialRot = this.entity.getLocalEulerAngles().clone();

    this.tween = null;

    app.on(this.event, function () {
        this.start();
    }, this);
    
    this.on('enable', function () {
        this.entity.setPosition(this.initialPos);
        this.entity.setLocalScale(this.initialScl);
        this.entity.setLocalEulerAngles(this.initialRot);
    });

    this.on('attr', function (name, value, prev) {
        this.start();
    });
};

Tweener.prototype.start = function() {
    var app = this.app;

    var easingTypes = [ 'In', 'Out', 'InOut' ];
    var easingFuncs = [ 'Linear', 'Quadratic', 'Cubic', 'Quartic', 'Quintic', 'Sinusoidal', 'Exponential', 'Circular', 'Elastic', 'Back', 'Bounce'];

    var easingFunc;
    if (this.easingFunction === 0) {
        easingFunc = TWEEN.Easing[easingFuncs[this.easingFunction]].None;
    } else {
        easingFunc = TWEEN.Easing[easingFuncs[this.easingFunction]][easingTypes[this.easingType]];
    }

    if (this.tween) {
        this.tween.stop();
    }

    var startState = { 
        px: this.startPos.x, py: this.startPos.y, pz: this.startPos.z,
        sx: this.startScale.x, sy: this.startScale.y, sz: this.startScale.z, 
        rx: this.startRot.x, ry: this.startRot.y, rz: this.startRot.z
    };
    var endState = {
        px: this.endPos.x, py: this.endPos.y, pz: this.endPos.z,
        sx: this.endScale.x, sy: this.endScale.y, sz: this.endScale.z, 
        rx: this.endRot.x, ry: this.endRot.y, rz: this.endRot.z 
    };

    var e = this.entity;

    this.tween = new TWEEN.Tween(startState)
        .to(endState, this.duration)
        .easing(easingFunc)
        .onStart(function (obj) {
            if (this.startEvent !== '') {
                app.fire(this.startEvent);
            }
        }.bind(this))
        .onStop(function (obj) {
            if (this.stopEvent !== '') {
                app.fire(this.stopEvent);
            }
        }.bind(this))
        .onUpdate(function (obj) {
            e.setPosition(obj.px, obj.py, obj.pz);
            e.setLocalScale(obj.sx, obj.sy, obj.sz);
            e.setEulerAngles(obj.rx, obj.ry, obj.rz);
            if (this.updateEvent !== '') {
                app.fire(this.updateEvent);
            }
        }.bind(this))
        .onComplete(function (obj) {
            if (this.completeEvent !== '') {
                app.fire(this.completeEvent);
            }
        }.bind(this))
        .onRepeat(function (obj) {
            if (this.repeatEvent !== '') {
                app.fire(this.repeatEvent);
            }
        }.bind(this))
        .repeat(this.repeat)
        .repeatDelay(this.repeatDelay)
        .yoyo(this.yoyo)
        .delay(this.delay)
        .start();
};


