// SwitchingMaterials.js
// More information about materials: http://developer.playcanvas.com/en/tutorials/beginner/basic-materials/
var SwitchingMaterials = pc.createScript('switchingMaterials');

// Create an array of materials to cycle the model through
SwitchingMaterials.attributes.add("materials", {
    type: "asset", 
    assetType: "material", 
    array: true, 
    title: "Materials"});

// initialize code called once per entity
SwitchingMaterials.prototype.initialize = function() {
    var self = this;
    
    this.materialIndex = 0;
    
    var onMaterialSwitch = function(index) {
        self.changeMaterial(index);
    };

    // listen for the player:move event
    this.app.on('uimanager:switch', onMaterialSwitch); 
    
    var assets = this.app.assets.findByTag('color');
    
    // Start loading all the assets
    for(var i = 0; i < assets.length; i++) {
        if (!assets[i].resource) {
            this.app.assets.load(assets[i]);
        }
    }
};

SwitchingMaterials.prototype.changeMaterial = function(index) 
{
    if(index < this.materials.length && index >= 0)
    {
        // Get the next material asset in the array 
        this.materialIndex = index;
        var material = this.materials[this.materialIndex];        

        // Assign the material to all the mesh instances in the model
        var meshInstances = this.entity.model.meshInstances;
        for (var i = 0; i < meshInstances.length; ++i) { 
            var mesh = meshInstances[i];
            mesh.material = material.resource;
        }
    }
};

SwitchingMaterials.prototype.postInitialize = function() {    
    var self = this;
    
    this.app.on('button:displaywhite', function () {
        self.changeMaterial(0);
    }, this);
    
    this.app.on('button:displayorange', function () {
        self.changeMaterial(1);
    }, this);
    
    this.app.on('button:displayblue', function () {
        self.changeMaterial(2);
    }, this);
    
    this.app.on('button:displaygreen', function () {
        self.changeMaterial(3);
    }, this);  
};

SwitchingMaterials.prototype.changeToNextMaterial = function(dt) {
    // Get the next material asset in the array 
    this.materialIndex = (this.materialIndex + 1) % this.materials.length;
    var material = this.materials[this.materialIndex];        

    // Assign the material to all the mesh instances in the model
    var meshInstances = this.entity.model.meshInstances;
    for (var i = 0; i < meshInstances.length; ++i) { 
        var mesh = meshInstances[i];
        mesh.material = material.resource;
    }
};



