// ui-battery.js

var Ui = pc.createScript('uiBattery');

Ui.attributes.add('css', {type: 'asset', assetType:'css', title: 'CSS Asset'});
Ui.attributes.add('html', {type: 'asset', assetType:'html', title: 'html-battery'});
Ui.attributes.add('buttonSound', { type: 'string' });


Ui.prototype.initialize = function () {
    
    var app = this.app;
    
    // create STYLE element
    var style = document.createElement('style');

    // append to head
    document.head.appendChild(style);
    style.innerHTML = this.css.resource || '';
    
    // Add the HTML
    this.div = document.createElement('div');
    this.div.classList.add('container');
    this.div.innerHTML = this.html.resource || '';
    
    // Replace tags in the html 
    // header
    this.div.innerHTML = this.div.innerHTML.replace('{closex-image}', app.assets.find('close.png').getFileUrl());
   
    // product
    this.div.innerHTML = this.div.innerHTML.replace('{pedals-image}', app.assets.find('pedals.png').getFileUrl());
  
    this.pageIsShown = false;
    
};

Ui.prototype.showPage = function() {
    if(this.pageIsShown)
        return;
    
    this.pageIsShown = true;
    
    var app = this.app;
    var self = this;
    
    document.body.appendChild(this.div);
    
    this.btnXClose = document.getElementById('btn-closeX');
    if(this.btnXClose) {
        this.btnXClose.onclick = function() { 
            app.fire('game:playSound', self.buttonSound);
            self.hidePage();
            app.fire('game:closeBatteryInfoPage');
        };
    }
    
    this.title = document.getElementById('batterytitle');
    if(this.title) {
       this.title.innerHTML = "500w/h battery";
    }
};

Ui.prototype.showPage2 = function() {
    if(this.pageIsShown)
        return;
    
    this.pageIsShown = true;
    
    var app = this.app;
    var self = this;
    
    //Just change a text/title here, we will call the first or the other depending on the choice
    //
    //
    document.body.appendChild(this.div);
    
    this.btnXClose = document.getElementById('btn-closeX');
    if(this.btnXClose) {
        this.btnXClose.onclick = function() { 
            app.fire('game:playSound', self.buttonSound);
            self.hidePage();
            app.fire('game:closeBatteryInfoPage');
        };
    }
    
    this.title = document.getElementById('batterytitle');
    if(this.title) {
       this.title.innerHTML = "1000w/h battery";
    }
};

Ui.prototype.hidePage = function() {
    if(!this.pageIsShown)
        return;
    
    this.pageIsShown = false;

    if(this.btnXClose) {
        this.btnXClose.onclick = null;
    }
    
    document.body.removeChild(this.div);
};


