// ui-pay.js

var Ui = pc.createScript('uiPay');

Ui.attributes.add('css', {type: 'asset', assetType:'css', title: 'CSS Asset'});
Ui.attributes.add('html', {type: 'asset', assetType:'html', title: 'html-Pay'});
Ui.attributes.add('buttonSound', { type: 'string' });

Ui.prototype.initialize = function () {
    
    var app = this.app;
    
    // create STYLE element
    var style = document.createElement('style');

    // append to head
    document.head.appendChild(style);
    style.innerHTML = this.css.resource || '';
    
    // Add the HTML
    this.div = document.createElement('div');
    this.div.classList.add('container', 'container-grey');
    this.div.innerHTML = this.html.resource || '';
    
    // Replace tags in the html 
    // header

    var blueArrow = app.assets.find('blue-arrow.png').getFileUrl();
    this.div.innerHTML = this.div.innerHTML.replace('{blue-arrow-image}', blueArrow);
    this.div.innerHTML = this.div.innerHTML.replace('{blue-arrow-image2}', blueArrow);
    this.div.innerHTML = this.div.innerHTML.replace('{blue-arrow-image3}', blueArrow);
    this.div.innerHTML = this.div.innerHTML.replace('{blue-arrow-image4}', blueArrow);
 
    
    this.div.innerHTML = this.div.innerHTML.replace('{applepayongrey-image}', app.assets.find('applepayongrey.png').getFileUrl());
    this.div.innerHTML = this.div.innerHTML.replace('{chasecard-image}', app.assets.find('chasecard.png').getFileUrl());

    this.div.innerHTML = this.div.innerHTML.replace('{applefaceid-image}', app.assets.find('applefaceid.png').getFileUrl());
    
    this.pageIsShown = false;
};

Ui.prototype.showPage = function() {
    if(this.pageIsShown)
        return;
    
    this.pageIsShown = true;
    
    var app = this.app;
    var self = this;
    
    document.body.appendChild(this.div);
    
    this.btnXClose = document.getElementById('btn-closeX');
    if(this.btnXClose) {
        this.btnXClose.onclick = function() { 
            app.fire('game:playSound', self.buttonSound);
            self.hidePage();
            app.fire('game:closePayPage');
        };
    }
};

Ui.prototype.hidePage = function() {
    if(!this.pageIsShown)
        return;
    
    this.pageIsShown = false;

    if(this.btnXClose) {
        this.btnXClose.onclick = null;
    }
    
    document.body.removeChild(this.div);
};


