// ui-buy.js

var Ui = pc.createScript('uiBuy');

Ui.attributes.add('css', {type: 'asset', assetType:'css', title: 'CSS Asset'});
Ui.attributes.add('html', {type: 'asset', assetType:'html', title: 'html-buy'});
Ui.attributes.add('buttonSound', { type: 'string' });


Ui.prototype.initialize = function () {
    
    var app = this.app;
    
    // create STYLE element
    var style = document.createElement('style');

    // append to head
    document.head.appendChild(style);
    style.innerHTML = this.css.resource || '';
    
    // Add the HTML
    this.div = document.createElement('div');
    this.div.classList.add('container');
    this.div.innerHTML = this.html.resource || '';
    
    var path3 = app.assets.find('path3@3x.png').getFileUrl();
    
    // Replace tags in the html 
    // header
    this.div.innerHTML = this.div.innerHTML.replace('{closex-image}', app.assets.find('close.png').getFileUrl());
    this.div.innerHTML = this.div.innerHTML.replace('{arrow-left}', app.assets.find('arrow-left.png').getFileUrl());
    this.div.innerHTML = this.div.innerHTML.replace('{cart-image}', app.assets.find('cart.png').getFileUrl());
    this.div.innerHTML = this.div.innerHTML.replace('{arrow}', path3);
    this.div.innerHTML = this.div.innerHTML.replace('{arrow2}', path3);
    this.div.innerHTML = this.div.innerHTML.replace('{arrow3}', path3);
    this.div.innerHTML = this.div.innerHTML.replace('{arrow4}', path3);
    this.div.innerHTML = this.div.innerHTML.replace('{arrow5}', path3);
    
    // product
    this.div.innerHTML = this.div.innerHTML.replace('{ebike-image}', app.assets.find('ebike.png').getFileUrl());
    
    var star_activePath = app.assets.find('star_active.png').getFileUrl();
    
    // review stars
    this.div.innerHTML = this.div.innerHTML.replace('{star-image}', star_activePath);
    this.div.innerHTML = this.div.innerHTML.replace('{star-image2}', star_activePath);
    this.div.innerHTML = this.div.innerHTML.replace('{star-image3}', star_activePath);
    this.div.innerHTML = this.div.innerHTML.replace('{star-image4}', star_activePath);
    this.div.innerHTML = this.div.innerHTML.replace('{star-image5}', star_activePath);
    
    this.div.innerHTML = this.div.innerHTML.replace('{stars5-image}', app.assets.find('stars5.png').getFileUrl());
    
    this.pageIsShown = false;
    
};


Ui.prototype.showPage = function() {
    if(this.pageIsShown)
        return;
    
    this.pageIsShown = true;
    
    var app = this.app;
    var self = this;
    
    document.body.appendChild(this.div);
    
    console.log('showing buy page 1');
    
    this.btnXClose = document.getElementById('btn-closeX');
    if(this.btnXClose) {
        this.btnXClose.onclick = function() { 
            app.fire('game:playSound', self.buttonSound);
            self.hidePage();
            app.fire('game:closeBuyPage');
        };
    }
    
    console.log('showing buy page 2');
    // button add opens addpage
    this.btnAdd = document.getElementById('btn-add');
    if(this.btnAdd) {
        this.btnAdd.onclick = function() { 
            app.fire('game:playSound', self.buttonSound);
            self.hidePage();
            app.fire('game:openAddPage');
        };
    }
    console.log('showing buy page 3');
};

Ui.prototype.hidePage = function() {
    if(!this.pageIsShown)
        return;
    
    console.log('hidding buy page');
    
    this.pageIsShown = false;

    if(this.btnXClose) {
        this.btnXClose.onclick = null;
    }
    
    if(this.btnAdd) {
        this.btnAdd.onclick = null;
    }
    
    document.body.removeChild(this.div);
};


