// multiMesh.js
var MultiMesh = pc.createScript('multiMesh');

MultiMesh.attributes.add('LOD', { type: 'number', default: 0 });

// initialize code called once per entity
MultiMesh.prototype.initialize = function() {
    
    this.dict = {};
    
    var meshInstances = this.entity.model.meshInstances;
    console.log('Total Nb mesh instances ' + meshInstances.length);
    
    var i = 0;
    for (i = 0; i < meshInstances.length; ++i) { 
        mesh = meshInstances[i];
        //console.log('mesh: ' + mesh.node.name);
        
        var index = mesh.node.name.lastIndexOf("_");
        var name = mesh.node.name.substr(0, index);
        var type = mesh.node.name.substr(index+1, mesh.node.name.length);
        
        if(type !== 'LOD' + this.LOD)
            mesh.visible = false;
           
        if(!(name in this.dict))
            this.dict[name] = [];
        
        this.dict[name].push([type, i]);
    }
    
    var count = 0;
    for(var key in this.dict) {
        
        console.log('key: ' + key);
        var arr = this.dict[key];

        for (i = 0; i < arr.length; ++i) { 
            if(meshInstances[arr[i][1]].visible) { 
                console.log('sub: ' + arr[i][0] + ' index: ' + arr[i][1] + ' name: ' + meshInstances[arr[i][1]].node.name);
                ++count;
            }
        }
    }
    
    console.log('Nb mesh instances drawed ' + count);
};



