// LineBtwnEntityPos.js
var LineBtwnEntityPos = pc.createScript('lineBtwnEntityPos');

LineBtwnEntityPos.attributes.add('from', { type: 'entity' });

LineBtwnEntityPos.attributes.add('to', {
    type: 'vec3'
});

LineBtwnEntityPos.attributes.add('rayColor', {
    type:"rgb",
    default:[1,0,0]
});

// initialize code called once per entity
LineBtwnEntityPos.prototype.initialize = function() {
    
};

// update code called every frame
LineBtwnEntityPos.prototype.update = function(dt) {
    this.app.renderLine(this.from.getPosition(), this.to, this.rayColor);
};

