// touchInput.js
var TouchInput = pc.createScript('touchInput');

TouchInput.attributes.add('cameraEntity', {type: 'entity', title: 'Camera Entity'});
TouchInput.attributes.add('orbitSensitivity', {
    type: 'number', 
    default: 0.3, 
    title: 'Orbit Sensitivity', 
    description: 'How fast the object moves around its pivot. Higher is faster'
});

TouchInput.attributes.add('distanceSensitivity', {
    type: 'number', 
    default: 0.2, 
    title: 'Distance Sensitivity', 
    description: 'How fast the object moves in and out. Higher is faster'
});

TouchInput.attributes.add('zoomSensibility', {
    type: 'number', 
    default: 0.2, 
    title: 'Zoom Sensitivity'
});

TouchInput.attributes.add('pinchSensitivity', {
    type: 'number', 
    default: 2, 
    title: 'Pinch Sensitivity',
    description: 'How fast the object zooms in and out. Higher is faster'
});

TouchInput.attributes.add('confidenceFingerCountLimit', {
    type: 'number', 
    default: 5, 
    title: 'ConfidenceOnFingerCount', 
    description: 'number of frames needed to confirm nb of fingers'
});

TouchInput.attributes.add('confidenceOpCountLimit', {
    type: 'number', 
    default: 5, 
    title: 'ConfidenceOpCount', 
    description: 'number of frames needed to confirm operation'
});


// initialize code called once per entity
TouchInput.prototype.initialize = function() {
    this.lastTouchPoint = new pc.Vec2();
    this.lastPinchMidPoint = new pc.Vec2();
    this.lastPinchDistance = 0;
    this.distance = 0;
    this.isTranslation = false;
    this.twoFingersAction = false;
    this.confidenceFingerCount = 0;
    this.confidenceOpCount = 0;
    this.opValidated = false;
    
    if (this.app.touch) {
        this.app.touch.on(pc.EVENT_TOUCHSTART, this.onTouchStart, this);
        this.app.touch.on(pc.EVENT_TOUCHMOVE, this.onTouchMove, this);
        this.app.touch.on(pc.EVENT_TOUCHEND, this.onTouchStop, this);
        this.app.touch.on(pc.EVENT_TOUCHCANCEL, this.onTouchStop, this);
    }
    
    this.on('destroy', function() {
        if (this.app.touch) {
            this.app.touch.off(pc.EVENT_TOUCHSTART, this.onTouchStart, this);
            this.app.touch.off(pc.EVENT_TOUCHMOVE, this.onTouchMove, this);
            this.app.touch.off(pc.EVENT_TOUCHEND, this.onTouchStop, this);
            this.app.touch.off(pc.EVENT_TOUCHCANCEL, this.onTouchStop, this);
        }
    });
};

TouchInput.prototype.onTouchStart = function (event) {
    var touch = event.touches[0];
    this.lastTouchPoint.set(touch.x, touch.y);
    
     // We only care about the first touch for camera rotation. As the user touches the screen, 
    // we stored the current touch position
    var touches = event.touches;
    if (touches.length == 1) {
        this.lastTouchPoint.set(touches[0].x, touches[0].y);
    
    } else if (touches.length == 2) {
        // If there are 2 touches on the screen, then set the pinch distance
        this.lastPinchDistance = this.getPinchDistance(touches[0], touches[1]);
        this.calcMidPoint(touches[0], touches[1], this.lastPinchMidPoint);
    }
};

TouchInput.prototype.onTouchStop = function (event) {
    this.isTranslation = false;
    this.twoFingersAction = false;
    this.confidenceFingerCount = 0;
    this.confidenceOpCount = 0;
    this.opValidated = false;
};

TouchInput.pinchMidPoint = new pc.Vec2();
TouchInput.posDiff = new pc.Vec3();

TouchInput.prototype.onTouchMove = function (event) {
    
    this.confidenceFingerCount++;
    if(this.confidenceFingerCount < this.confidenceFingerCountLimit)
        return;
    
    var pinchMidPoint = TouchInput.pinchMidPoint;
    var posDiff = TouchInput.posDiff;
    
    var touches = event.touches;
    if (touches.length == 1) {
        if(this.twoFingersAction)
            return;
            
        var touch = event.touches[0];
        var dx = touch.x - this.lastTouchPoint.x;
        var dy = touch.y - this.lastTouchPoint.y;
        

        this.rotate(dx, dy);

        this.lastTouchPoint.set(touch.x, touch.y);
    
    } else if (touches.length == 2) { 
        this.twoFingersAction = true;
        
        // Calculate the difference in pinch distance since the last event
        var currentPinchDistance = this.getPinchDistance(touches[0], touches[1]);
        var diffInPinchDistance = currentPinchDistance - this.lastPinchDistance;
        this.lastPinchDistance = currentPinchDistance;
      
        var camera = this.cameraEntity.camera;
        posDiff.sub2(this.cameraEntity.getPosition(), this.entity.getPosition());
        this.distance = posDiff.length();
        
        if (!this.opValidated) {
            
            if (Math.abs(diffInPinchDistance) < this.pinchSensitivity) { 

                //resetting
                if(!this.isTranslation)
                    this.confidenceOpCount = 0;

                this.isTranslation = true;

                //validating op
                this.confidenceOpCount++;
                if(this.confidenceOpCount < this.confidenceOpCountLimit)
                    return;

                //console.log('----Translate-----');
                this.opValidated = true;
            }
            else { 
                //resetting
                if(this.isTranslation)
                    this.confidenceOpCount = 0;

                this.isTranslation = false;

                //validating op
                this.confidenceOpCount++;
                if(this.confidenceOpCount < this.confidenceOpCountLimit)
                    return;

                //console.log('----Zooming-----');
                this.opValidated = true;
            }
        }

        //if op is not validated, do nothing
        if(!this.opValidated)
            return;
        
        this.calcMidPoint(touches[0], touches[1], pinchMidPoint);
        
        if(this.isTranslation) { 
            this.pan(pinchMidPoint); 
        } else { 
            var zoomQty = this.distance = (diffInPinchDistance * this.distanceSensitivity * 0.1) * (this.distance * 0.1); 
            this.zoom(zoomQty * this.zoomSensibility);
        }
        
        this.lastPinchMidPoint.copy(pinchMidPoint);
    }
};

TouchInput.horizontalQuat = new pc.Quat();
TouchInput.verticalQuat = new pc.Quat();
TouchInput.resultQuat = new pc.Quat();
TouchInput.transformedForward = new pc.Vec3();

TouchInput.prototype.rotate = function (dx, dy) {
    var horzQuat = TouchInput.horizontalQuat;
    var vertQuat = TouchInput.verticalQuat;
    var resultQuat = TouchInput.resultQuat;

    // Create a rotation around the camera's orientation in order for them to be in 
    // screen space  
    horzQuat.setFromAxisAngle(this.cameraEntity.up, dx * this.orbitSensitivity);
    vertQuat.setFromAxisAngle(this.cameraEntity.right, dy * this.orbitSensitivity);

    // Apply both the rotations to the existing entity rotation
    resultQuat.mul2(horzQuat, vertQuat);
    resultQuat.mul(this.entity.getRotation());

    var yaw = this.getYaw(resultQuat);
    this.entity.setRotation(new pc.Quat().setFromEulerAngles(0, yaw, 0));     
};

TouchInput.prototype.getYaw = function (quat) {
    var transformedForward = TouchInput.transformedForward;
    quat.transformVector(pc.Vec3.FORWARD, transformedForward);

    return Math.atan2(-transformedForward.x, -transformedForward.z) * pc.math.RAD_TO_DEG;    
};

TouchInput.fromWorldPoint = new pc.Vec3();
TouchInput.toWorldPoint = new pc.Vec3();
TouchInput.worldDiff = new pc.Vec3();

TouchInput.prototype.pan = function(midPoint) {
    var fromWorldPoint = TouchInput.fromWorldPoint;
    var toWorldPoint = TouchInput.toWorldPoint;
    var worldDiff = TouchInput.worldDiff;
    
    // For panning to work at any zoom level, we use screen point to world projection
    // to work out how far we need to pan the pivotEntity in world space 
    var camera = this.cameraEntity.camera;

    camera.screenToWorld(midPoint.x, midPoint.y, this.distance, fromWorldPoint);
    camera.screenToWorld(this.lastPinchMidPoint.x, this.lastPinchMidPoint.y, this.distance, toWorldPoint);
    
    worldDiff.sub2(fromWorldPoint, toWorldPoint);
    var pos = this.entity.getLocalPosition();

    this.entity.setLocalPosition(pos.x + worldDiff.x, pos.y, pos.z + worldDiff.z);
};

TouchInput.prototype.zoom = function(zoomValue) {
    var scale = this.entity.getLocalScale();
    this.entity.setLocalScale(scale.x + zoomValue, scale.y + zoomValue, scale.z + zoomValue);
};

TouchInput.prototype.getPinchDistance = function (pointA, pointB) {
    // Return the distance between the two points
    var dx = pointA.x - pointB.x;
    var dy = pointA.y - pointB.y;    
    
    return Math.sqrt((dx * dx) + (dy * dy));
};

TouchInput.prototype.calcMidPoint = function (pointA, pointB, result) {
    result.set(pointB.x - pointA.x, pointB.y - pointA.y);
    result.scale(0.5);
    result.x += pointA.x;
    result.y += pointA.y;
};


