// taprecenter.js
/*jshint esversion: 6, asi: true, laxbreak: true*/

// taprecenter.js: Defines a playcanvas script that re-centers the AR scene when the screen is
// tapped.

var taprecenter = pc.createScript('taprecenter')

taprecenter.attributes.add('toSpawn', { type: 'entity', title: "spawned Item", description: "Item to spawn" });

// Fire a 'recenter' event to move the camera back to its starting location in the scene.
taprecenter.prototype.initialize = function() {
    var app = this.app;
    
    app.on('taprecenter:ready', function () {
        if(pc.platform.mobile)
            app.touch.on(pc.EVENT_TOUCHSTART, this.touchStart, this);
    }, this);
}

taprecenter.prototype.touchStart = function (event) {
    var app = this.app;
    
    if (event.touches.length !== 1) 
        return;

    if(this.toSpawn)
        this.toSpawn.enabled = true;
    
    app.fire('game:hidetutorial');
    app.fire('game:activatephysics');
    
    app.touch.off(pc.EVENT_TOUCHSTART, this.touchStart, this);
    
};


