// UIMgr.js
var UIMgr = pc.createScript('uiMgr');

UIMgr.attributes.add('checkMarkMotor', { type: 'entity' });
UIMgr.attributes.add('checkMarkBattery', { type: 'entity' });
UIMgr.attributes.add('checkMarkType', { type: 'entity' });
UIMgr.attributes.add('checkMarkColor', { type: 'entity' });

UIMgr.attributes.add('checkMarkBlue', { type: 'entity' });
UIMgr.attributes.add('checkMarkOrange', { type: 'entity' });
UIMgr.attributes.add('checkMarkGreen', { type: 'entity' });
UIMgr.attributes.add('checkMarkWhite', { type: 'entity' });

UIMgr.attributes.add('firstOptionText', { type: 'entity' });
UIMgr.attributes.add('secondOptionText', { type: 'entity' });
UIMgr.attributes.add('videoText', { type: 'entity' });
UIMgr.attributes.add('firstOptionCheckMark', { type: 'entity' });
UIMgr.attributes.add('secondOptionCheckMark', { type: 'entity' });

UIMgr.attributes.add('leftBoard', { type: 'entity' });
UIMgr.attributes.add('pos1', { type: 'vec3' });
UIMgr.attributes.add('togo1', { type: 'vec3' });

UIMgr.attributes.add('rightBoard', { type: 'entity' });
UIMgr.attributes.add('pos2', { type: 'vec3' });
UIMgr.attributes.add('togo2', { type: 'vec3' });

UIMgr.attributes.add('optionBoard1', { type: 'entity' });
UIMgr.attributes.add('pos3', { type: 'vec3' });
UIMgr.attributes.add('togo3Portrait', { type: 'vec3' });
UIMgr.attributes.add('togo3Landscape', { type: 'vec3' });

UIMgr.attributes.add('optionBoard2', { type: 'entity' });
UIMgr.attributes.add('pos4', { type: 'vec3' });
UIMgr.attributes.add('togo4Portrait', { type: 'vec3' });
UIMgr.attributes.add('togo4Landscape', { type: 'vec3' });

UIMgr.attributes.add('durationIn', {
    type: 'number',
    default: 0.1,
    description: 'Tween animation duration'
});

UIMgr.attributes.add('durationOut', {
    type: 'number',
    default: 0.1,
    description: 'Tween animation duration'
});

// initialize code called once per entity
UIMgr.prototype.initialize = function() {

    var app = this.app;
    
    this.viewMode = 'Portrait';

    this.isMainVisible = false;
    this.isOption1Visible = false;
    this.isOption2Visible = false;
    
    this.isDone1 = false;
    this.tween1 = null;
    
    this.isDone2 = false;
    this.tween2 = null;
    
    this.tween3 = null;
    this.tween4 = null;
    
    app.on('uiMgr:showMain', function () {
        if(!this.isMainVisible)
            this.showMain();
    }, this);
    
    app.on('uiMgr:hideMain', function () {
        if(this.isMainVisible)
            this.hideMain();
    }, this);
    
    app.on('uiMgr:showOption1', function () {
        if(!this.isOption1Visible)
            this.showOption1();
    }, this);
    
    app.on('uiMgr:hideOption1', function () {
        if(this.isOption1Visible)
            this.hideOption1();
    }, this);
    
    app.on('uiMgr:showColors', function () {
        if(!this.isOption2Visible)
            this.showOption2();
        else
            this.hideOption2();
    }, this);
    
    app.on('uiMgr:showOption2', function () {
        if(!this.isOption2Visible)
            this.showOption2();
    }, this);
    
    app.on('uiMgr:hideOption2', function () {
        if(this.isOption2Visible)
            this.hideOption2();
    }, this);
    
    app.on('uiMgr:checkmarkMotor', function () {
        this.checkMarkMotor.enabled = true;
    }, this);
    
    app.on('uiMgr:checkmarkBattery', function () {
        this.checkMarkBattery.enabled = true;
    }, this);
    
    app.on('uiMgr:checkmarkType', function () {
        this.checkMarkType.enabled = true;
    }, this);
    
    app.on('uiMgr:checkMarkColor', function () {
        this.checkMarkColor.enabled = true;
    }, this);
    
    app.on('uiMgr:checkMarkBlue', function (val) {
        this.checkMarkBlue.enabled = val;
    }, this);
    
    app.on('uiMgr:checkMarkGreen', function (val) {
        this.checkMarkGreen.enabled = val;
    }, this);
    
    app.on('uiMgr:checkMarkOrange', function (val) {
        this.checkMarkOrange.enabled = val;
    }, this);
    
    app.on('uiMgr:checkMarkWhite', function (val) {
        this.checkMarkWhite.enabled = val;
    }, this);
    
    app.on('uiMgr:firstOptionText', function (val) {
        this.firstOptionText.element.text = val;
    }, this);
    
    app.on('uiMgr:secondOptionText', function (val) {
        this.secondOptionText.element.text = val;
    }, this);
    
    app.on('uiMgr:firstOptionCheckMark', function (val) {
        this.firstOptionCheckMark.enabled = val;
    }, this);
    
    app.on('uiMgr:secondOptionCheckMark', function (val) {
        this.secondOptionCheckMark.enabled = val;
    }, this);
    
    app.on('uiMgr:videoText', function (val) {
        this.videoText.element.text = val;
    }, this);
    
    app.on('game:landscape', function () {
        if(this.viewMode === 'Landscape')
            return;
        
        this.viewMode = 'Landscape';
        
        //we can see it, need to move it
        if(this.isOption1Visible) {
            
            if(this.tween3)
                this.tween3.stop();
            
            this.isOption1Running = false;
            this.showOption1();
            
        } else if(this.isOption2Visible) {
            
            if(this.tween4)
                this.tween4.stop();
            
            this.isOption2Running = false;
            this.showOption2();
        }
        
    }, this);
    
    app.on('game:portrait', function () {
        if(this.viewMode === 'Portrait')
            return;
        
        this.viewMode = 'Portrait';
        
        //we can see it, need to move it
        if(this.isOption1Visible) {
            
            if(this.tween3)
                this.tween3.stop();
            
            this.isOption1Running = false;
            this.showOption1();
        }
        else if(this.isOption2Visible) {
            
            if(this.tween4)
                this.tween4.stop();
            
            this.isOption2Running = false;
            this.showOption2();
        }
        
    }, this);
};

UIMgr.prototype.showMain = function () {

    var self = this;
    
    if(!this.isMainRunning) {
        this.isMainRunning = true;
        this.isMainVisible = true;

        tween1 = this.leftBoard.tween(this.leftBoard.getLocalPosition()).to({x: this.togo1.x, y: this.togo1.y, z: this.togo1.z}, this.durationIn, pc.SineInOut);
        tween2 = this.rightBoard.tween(this.rightBoard.getLocalPosition()).to({x: this.togo2.x, y: this.togo2.y, z: this.togo2.z}, this.durationIn, pc.SineInOut);
        
        this.isDone1 = false;
        this.isDone2 = false;

        tween1.on('complete', function () {
            self.isDone1 = true;
        });  
        
        tween2.on('complete', function () {
            self.isDone2 = true;
        }); 
   
        tween1.start();
        tween2.start();
    } 
};

UIMgr.prototype.hideMain = function () {

    var self = this;
    
    if(!this.isMainRunning) {
        this.isMainRunning = true;
        this.isMainVisible = false;
        
        tween1 = this.leftBoard.tween(this.leftBoard.getLocalPosition()).to({x: this.pos1.x, y: this.pos1.y, z: this.pos1.z}, this.durationOut, pc.SineInOut);
        tween2 = this.rightBoard.tween(this.rightBoard.getLocalPosition()).to({x: this.pos2.x, y: this.pos2.y, z: this.pos2.z}, this.durationOut, pc.SineInOut);

        this.isDone1 = false;
        this.isDone2 = false;

        tween1.on('complete', function () {
            self.isDone1 = true;
        });  
        
        tween2.on('complete', function () {
            self.isDone2 = true;
        }); 
   
        tween1.start();
        tween2.start();
    }
    
};

UIMgr.prototype.showOption1 = function () {

    var self = this;
    
    if(!this.isOption1Running) {
        this.isOption1Running = true;
        this.isOption1Visible = true;

        var togo = this.viewMode !== 'Portrait' ? this.togo3Landscape : this.togo3Portrait;
        
        tween3 = this.optionBoard1.tween(this.optionBoard1.getLocalPosition()).to({x: togo.x, y: togo.y, z: togo.z}, this.durationIn, pc.SineInOut);
        
        tween3.on('complete', function () {
            self.isOption1Running = false;
        });  
        
        tween3.start();
    } 
};

UIMgr.prototype.hideOption1 = function () {

    var self = this;
    
    if(!this.isOption1Running) {
        this.isOption1Running = true;
        this.isOption1Visible = false;
        
        tween3 = this.optionBoard1.tween(this.optionBoard1.getLocalPosition()).to({x: this.pos3.x, y: this.pos3.y, z: this.pos3.z}, this.durationOut, pc.SineInOut);

        tween3.on('complete', function () {
            self.isOption1Running = false;
        });  
   
        tween3.start();
    }
    
};

UIMgr.prototype.showOption2 = function () {

    var self = this;
    
    if(!this.isOption2Running) {
        this.isOption2Running = true;
        this.isOption2Visible = true;

        var togo = this.viewMode !== 'Portrait' ? this.togo4Landscape : this.togo4Portrait;
        
        tween4 = this.optionBoard2.tween(this.optionBoard2.getLocalPosition()).to({x: togo.x, y: togo.y, z: togo.z}, this.durationIn, pc.SineInOut);
        
        tween4.on('complete', function () {
            self.isOption2Running = false;
        });  
        
        tween4.start();
    } 
};

UIMgr.prototype.hideOption2 = function () {

    var self = this;
    
    if(!this.isOption2Running) {
        this.isOption2Running = true;
        this.isOption2Visible = false;
        
        tween4 = this.optionBoard2.tween(this.optionBoard2.getLocalPosition()).to({x: this.pos4.x, y: this.pos4.y, z: this.pos4.z}, this.durationOut, pc.SineInOut);

        tween4.on('complete', function () {
            self.isOption2Running = false;
        });  
        
        tween4.start();
    }
    
};

UIMgr.prototype.update = function(dt) {
    if(this.isMainRunning) {
        if(this.isDone1 && this.isDone2)
            this.isMainRunning = false;
    }
};

