// FadeSounds.js
var FadingSoundIn = pc.createScript('FadeSoundIn');

FadingSoundIn.attributes.add("duration", {type: "number", default: 2, title: "Duration (Secs)"});
FadingSoundIn.attributes.add('startingEvent', {
    title: 'Event',
    type: 'string'
});

// initialize code called once per entity
FadingSoundIn.prototype.initialize = function() {
    this.isRunning = false;
    
    this.app.on(this.startingEvent, function (soundName) {
        var slot = this.entity.sound.slot(soundName); 
        this.currentVolume = slot.volume;
        this.fadeIn(soundName);
        //console.log('FadeIn request received');
    }, this);
};

FadingSoundIn.prototype.fadeIn = function (soundName) {

    var self = this;
    
    // Get a reference to a soundslot
    var slot = this.entity.sound.slot(soundName);  
    
    if(!this.isRunning && slot && slot.instances[0])
    {
        this.isRunning = true;
        
        var data = {
            volume: slot.instances[0].volume
        };
        
        var tween = this.entity.tween(data).to({volume: this.currentVolume}, this.duration, pc.SineInOut);
        
        tween.on('update', function () {
             
            for (var i = 0; i < slot.instances.length; ++i) {
                var instance = slot.instances[i];

                // Change the volume of the audio based on the value of the curve at 
                // the current play time of the audio
                instance.volume = data.volume;
            }  
        }).on('complete', function () {
            self.isRunning = false;
        });
  
        tween.start();
    }
    
};

var FadingSoundOut = pc.createScript('FadeSoundOut');

FadingSoundOut.attributes.add("duration", {type: "number", default: 2, title: "Duration (Secs)"});
FadingSoundOut.attributes.add('startingEvent', {
    title: 'Event',
    type: 'string'
});

// initialize code called once per entity
FadingSoundOut.prototype.initialize = function() {
    this.isRunning = false;
    
    
    this.app.on(this.startingEvent, function (soundName) {
        var slot = this.entity.sound.slot(soundName); 
        this.currentVolume = slot.volume;
        this.fadeOut(soundName);
        //console.log('FadeOut request received');
    }, this);
};

FadingSoundOut.prototype.fadeOut = function (soundName) {

    var self = this;
    
    // Get a reference to a soundslot
    var slot = this.entity.sound.slot(soundName);  
    
    if(!this.isRunning && slot && slot.instances[0])
    {
        this.isRunning = true;
        
        var data = {
            volume: slot.instances[0].volume
        };
        
        var tween = this.entity.tween(data).to({volume: 0}, this.duration, pc.SineInOut);
        
        tween.on('update', function () {
             
            for (var i = 0; i < slot.instances.length; ++i) {
                var instance = slot.instances[i];

                // Change the volume of the audio based on the value of the curve at 
                // the current play time of the audio
                instance.volume = data.volume;
            }  
        }).on('complete', function () {
            self.isRunning = false;
        });
  
        tween.start();
    }
    
};

