// LineBtwnPos.js
var Line = pc.createScript('lineBtwnPos');

Line.attributes.add('from', {
    type: 'vec3'
});

Line.attributes.add('to', {
    type: 'vec3'
});

Line.attributes.add('rayColor', {
    type:"rgb",
    default:[1,0,0]
});

// initialize code called once per entity
Line.prototype.initialize = function() {
    
};

// update code called every frame
Line.prototype.update = function(dt) {
    this.app.renderLine(this.from, this.to, this.rayColor);
};

