// media-player.js
var MediaPlayer = pc.createScript('mediaPlayer');

MediaPlayer.attributes.add('screenMaterial', { type: 'asset', assetType: 'material', title: 'Material' });
MediaPlayer.attributes.add('url', { type: 'string', title: 'Url' });
MediaPlayer.attributes.add('videoToPlay', {
    title: 'Video',
    description: 'MP4 video asset to play back on this video texture.',
    type: 'asset'
});
MediaPlayer.attributes.add('playButton', { type: 'entity' });
MediaPlayer.attributes.add('stopButton', { type: 'entity' });
MediaPlayer.attributes.add('ignoreEvents', { type: 'boolean', default: false });
MediaPlayer.attributes.add('autoPlay', { type: 'boolean', default: false });
MediaPlayer.attributes.add('volume', {
    type: 'number',
    default: 1
});

MediaPlayer.prototype.createTexture = function () {
    var app = this.app;
    var i, texture, material;

    texture = new pc.Texture(app.graphicsDevice, {
        format: pc.PIXELFORMAT_R8_G8_B8,
        autoMipmap: false
    });
    texture.minFilter = pc.FILTER_LINEAR;
    texture.magFilter = pc.FILTER_LINEAR;
    texture.addressU = pc.ADDRESS_CLAMP_TO_EDGE;
    texture.addressV = pc.ADDRESS_CLAMP_TO_EDGE;

    if(this.screenMaterial) {
        material = this.screenMaterial.resource;
        material.emissiveMap = texture;
        material.update();
    }
    
    return texture;
};

MediaPlayer.prototype.initVideo = function (url) {
    this.video.src = url;
    this.video.volume = this.volume;
    this.play(false, false);
};

MediaPlayer.prototype.stopVideo = function () {
    this.stop(false);
};

MediaPlayer.prototype.initialize = function () {
    var app = this.app;
    var self = this;
    
    this.isVideo = false;
    this.updateEvery = 2;
    this.playing = false;
    this.frameCount = 0;

    var texture = this.createTexture();
    var video = document.createElement('video');
    var image = new Image();
    
    // muted attribute is required for videos to autoplay
    video.muted = true;
    
    // critical for iOS or the video won't initially play, and will go fullscreen when playing
    video.playsInline = true;
    this.isFromUrl = true;
    
    var ext = '';
    if(this.url) {
        ext = this.url.split('.').pop();
    }
    else if(this.videoToPlay) {
        ext = this.videoToPlay.getFileUrl().split('.').pop();
        ext = ext.split('?').shift();
        this.isFromUrl = false;
    }
    
    if (ext === "mp4" || this.isFromUrl) {
        video.addEventListener('canplay', function (e) {
            texture.setSource(video);
        });
        video.src = this.isFromUrl ? this.url : this.videoToPlay.getFileUrl();
        video.crossOrigin = 'anonymous';
        video.loop = true;
        video.preload = true;
        video.volume = this.volume;

        this.isVideo = true;
    } else {
        image.addEventListener('load', function (e) {
            texture.setSource(image);
        });
        image.crossOrigin = 'anonymous';
        image.src = this.url;

        this.isVideo = false;
    }
    
    this.texture = texture;
    this.image = image;
    this.video = video;
    
    app.on('media-player:playBtnClick', function (fromBtnClick, muted) {
        if(!this.ignoreEvents)
            this.play(fromBtnClick, muted);
    }, this);
    
    app.on('media-player:stopBtnClick', function (fromBtnClick) {
        if(!this.ignoreEvents)
            this.stop(fromBtnClick);
    }, this);
    
    app.on('media-player:muteVideo', function (fromBtnClick) {
        if(!this.ignoreEvents)
            this.mute(fromBtnClick);
    }, this);
    
    if(this.autoPlay)
        this.play(false, true);
    
    app.fire('pulse:start');
};

// Video API
MediaPlayer.prototype.isPlaying = function () {
    return this.isVideo && this.playing;
};

MediaPlayer.prototype.PlayButtonEnabled = function (val) {
    if(this.playButton)
        this.playButton.enabled = val;
};

MediaPlayer.prototype.StopButtonEnabled = function (val) {
    if(this.stopButton)
        this.stopButton.enabled = val;
};

MediaPlayer.prototype.play = function (fromBtnClick, muted) {
    var app = this.app;
    var self = this;
    
    if (this.isVideo) {
        
        if (!this.playing) {
            console.log('play video');

            if(!muted) {
                this.PlayButtonEnabled(false);
                this.StopButtonEnabled(true);
            }
            
            if(fromBtnClick) {
                if(!(pc.platform.mobile && !app.xr.active)) {
                    if(this.stopButton) {
                        this.stopButton.model.material.emissive = this.stopButton.script.hoverChangeColor.hoverColor;
                        this.stopButton.model.material.update();
                    }
                }
            }
            
            setTimeout(function () {
                self.playing = true;
                self.video.muted = muted;
                self.video.volume = self.volume;
                self.video.play();
                app.fire('pulse:start');

            }, 250);
        } else  {
            console.log('restart video');
            
            this.PlayButtonEnabled(false);
            this.StopButtonEnabled(true);
            
            if(fromBtnClick) {
                if(!(pc.platform.mobile && !app.xr.active)) {
                    if(this.stopButton) {
                        this.stopButton.model.material.emissive = this.stopButton.script.hoverChangeColor.hoverColor;
                        this.stopButton.model.material.update();
                    }
                }
            }
            
            setTimeout(function () {
                self.playing = true;
                self.video.load();
                self.video.muted = muted;
                self.video.volume = self.volume;
                self.video.play();
                app.fire('pulse:start');

            }, 250);
        }
    }
};

MediaPlayer.prototype.stop = function (fromBtnClick) {
    var app = this.app;
    var self = this;
    
    if (this.isVideo && this.playing) {
        console.log('stop video');
        
        this.StopButtonEnabled(false);
        this.PlayButtonEnabled(true);
        
        if(fromBtnClick) {
            if(!(pc.platform.mobile && !app.xr.active)) {
                if(this.playButton) {
                    this.playButton.model.material.emissive = this.playButton.script.hoverChangeColor.hoverColor;
                    this.playButton.model.material.update();
                }
            }
        }
        
        setTimeout(function () {
            self.playing = false;
            self.video.muted = true;
            self.video.load();
            app.fire('pulse:start');
            
            self.texture = self.createTexture();
            self.texture.setSource(self.video);
        }, 250);
    }
};

MediaPlayer.prototype.mute = function (fromBtnClick) {
    var app = this.app;
    var self = this;
    
    if(!this.video.muted) {
        
        if(fromBtnClick) {
            if(!(pc.platform.mobile && !app.xr.active)) {
                if(this.playButton) {
                    this.playButton.model.material.emissive = this.playButton.script.hoverChangeColor.hoverColor;
                    this.playButton.model.material.update();
                }
            }
        }
        
        this.StopButtonEnabled(false);
        this.PlayButtonEnabled(true);
        
        setTimeout(function () {
            self.video.muted = true;
            app.fire('pulse:start');
            
        }, 250);
    }
};

MediaPlayer.prototype.update = function (dt) {
    if (this.playing) {
        if (this.frameCount++ % this.updateEvery === 0)
            this.texture.upload();
    }
};

